.slider-navigation {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    height: 30px;
    .slider-navigation-item {
        transition: all, .5s, ease;
    //    width: 20px;
    //    height: 20px;
    //    border-radius: 50%;
    //    background: white;
        cursor: pointer;
        margin: 5px 4px;
    //    box-shadow: -9px 0px 7px black inset;
    //    position: relative;
    //    bottom: 0;
        &.current-step {
            color: black;
        }
    }
}

.slider-content {
    height: 100%;
    position: relative;
    div {
        //So the transition isn't jerky
        margin: 0;
        padding: 0;
        height: 100%;
        //Needed for the transition to work nicely,
        //so one doesn't appear below the other during the transition
        position: absolute;
        &:not(:first-child) {
            opacity: 0;
        }
    }
}